import Joi from '@hapi/joi';

import { BuildType } from '../../../constants';
import { BaseAndroidJob, BaseAndroidJobSchema } from '../baseJob';

interface AndroidKeystore {
  keystoreDataBase64: string;
  keystorePath: string;
  keystorePassword: string;
  keyAlias: string;
  keyPassword: string;
}

const AndroidKeystoreSchema = Joi.object().keys({
  keystore: Joi.object().keys({
    keystoreDataBase64: Joi.string().required(),
    keystorePath: Joi.string().required(),
    keystorePassword: Joi.string().required(),
    keyAlias: Joi.string().required(),
    keyPassword: Joi.string().required(),
  }).required(),
});

interface ManagedAndroidJob extends BaseAndroidJob {
  type: BuildType.Managed;
  packageJson: any;
  manifest: any;
  secrets: {
    keystore: AndroidKeystore;
  };
}

const ManagedAndroidJobSchema = BaseAndroidJobSchema.concat(
  Joi.object().keys({
    type: Joi.string().valid(BuildType.Managed),
    packageJson: Joi.object().unknown(true).required(),
    manifest: Joi.object().unknown(true).required(),
    secrets: Joi.object({ keystore: AndroidKeystoreSchema.required() }),
  }),
);

export {
  ManagedAndroidJob,
  ManagedAndroidJobSchema,
  AndroidKeystore,
};
